.burger.is-active span:first-child {
  transform: rotate(45deg);
  top: 6px;
}

.burger.is-active span:last-child {
  transform: rotate(-45deg);
  top: 6px;
}

.burger.is-active span:nth-child(2) {
  transform: scale(0);
}

.burger > span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #f08058;
  transition: transform 0.3s ease-in-out;
}

