.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.checkbox input:checked + label:before {
  border-color: #fff;
}

.checkbox input:checked + label:after {
  opacity: 1;
}

.checkbox label {
  position: relative;
  padding: 4px 0 0 45px;
  cursor: pointer;
  font-size: 14px;
  color: #fff;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.checkbox label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 24px;
  height: 24px;
  border: 2px solid rgba(255, 255, 255, 0.35);
  background: trasparent;
  transition: border-color .2s ease-in-out, opacity .2s ease-in-out;
}

.checkbox label:after {
  content: '';
  position: absolute;
  opacity: 0;
  top: 8px;
  left: 8px;
  width: 8px;
  height: 8px;
  background: #fff;
  transition: border-color 0.2s ease-in-out;
}

