}

.places__in {
  position: relative;
  border-radius: 50%;
  margin: 0 auto 40px;
  box-sizing: border-box;
}

.places__in:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  border-radius: 50%;
}

.places__in .places__center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.places__circle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 9;
  transform: rotateY(-180deg) rotateZ(-90deg);
}

.places__circle circle {
  fill: none;
}

.places__title-in {
  font-size: 20px;
  margin-bottom: 10px;
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 400;
  font-style: italic;
}

.places_white {
  color: #fff;
}

.places_white .places__title {
  font-size: 20px;
  margin-bottom: 20px;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.places_white .places__in {
  width: 140px;
  height: 140px;
}

.places_white .places__in:before {
  border: 10px solid #e46b40;
}

.places_white .places__circle {
  width: 140px;
  height: 140px;
}

.places_white .places__circle circle {
  stroke-dasharray: 790px;
  stroke-dashoffset: -440px;
  stroke-width: 10px;
  stroke: white;
  animation: countdown 5s linear infinite forwards;
}

.places_green {
  color: #303d4a;
}

.places_green .places__title {
  font-size: 20px;
  margin-bottom: 20px;
  font-family: "Ubuntu", Arial, Helvetica, serif;
  font-weight: 300;
}

.places_green .places__in {
  width: 380px;
  height: 380px;
}

.places_green .places__in:before {
  border: 10px solid rgba(110, 205, 190, 0.25);
}

.places_green .places__in span {
  font-size: 72px;
  font-family: "Lora", Arial, Helvetica, serif;
  font-weight: 700;
}

.places_green .places__circle {
  width: 380px;
  height: 380px;
}

.places_green .places__circle circle {
  stroke-dasharray: 790px;
  stroke-dashoffset: -440px;
  stroke: white;
  stroke-width: 4px;
  animation: countdown 5s linear infinite forwards;
}

.places_green .icon-decor-line-top {
  fill: #6ecdbe;
  max-width: 192px;
  width: 100%;
}

@keyframes countdown {
  from {
    stroke-dashoffset: -440px;
  }
  to {
    stroke-dashoffset: 0px;
  }
}

